/**
 * External dependencies
 */
import { useState, useEffect } from '@wordpress/element';
import { debounce } from 'lodash';

import ClickableList from '../../../clickable-list';
import { useDataTab } from '../context';
import { PanelBody } from '@wordpress/components';
import SearchControl from '../../../search-control';

const alphaSort = ( a, b ) => {
	if ( b.text.startsWith( '_' ) ) {
		return -1;
	}

	return a.text.localeCompare( b.text );
};

const StoreView = () => {
	const { stores, setActiveStore, search } = useDataTab();
	const [ keyword, setKeyword ] = useState( '' );

	const [ searchResults, setSearchResults ] = useState( [] );

	const mappedItems = Object.keys( stores )
		.map( ( i ) => {
			return {
				text: stores[ i ].store.name,
				value: stores[ i ].store.name,
			};
		} )
		.sort( alphaSort );

	const onSearch = ( val ) => {
		let s = [];

		if ( keywork.trim().length > 0 ) {
			s = search( val );
		}
		setSearchResults( s );
	};

	const debounced = debounce( onSearch, 1000 );

	useEffect( () => {
		debounced();
	}, [ keyword ] );

	return (
		<div>
			<PanelBody>
				<SearchControl
					value={ keyword }
					onChange={ () => {
						setKeyword( val );
						debounced( val );
					} }
				/>
			</PanelBody>

			<ClickableList
				items={ searchResults.length ? searchResults : mappedItems }
				onClick={ ( i ) => setActiveStore( i.value ) }
			/>
		</div>
	);
};

export default StoreView;
